<template lang="pug">
    .wrapper-inserts    
        .files-uploads(v-if="data.content.files.length")
            .header-uploads
                h3.title Arquivos anexados

            .list-files
                .file(v-for="file in data.content.files")
                    img.image(:src="file.dataURL")
                    .details
                        span.filename {{file.name}}
                        span.size {{file.size}}

        .list(v-if="Object.keys(data.content.list).length")
            .header
                h3.title {{data.content.list.title}}
            .items
                .item(v-for="item in data.content.list.items") 
                    span.text {{item.text}}

        .checklist(v-if="Object.keys(data.content.checklist).length")
            .header
                h3.title {{data.content.checklist.title}}

            .items
                .item(v-for="item in data.content.checklist.items") 
                    span.text {{item.text}}
</template>

<script>
    export default {
        name: 'w-nav',
        props: ['data'],
        data () {
            return {
                msg: 'Welcome to Your Vue.js App'
            }
        },
        methods: {
            
        }
    }
</script>
<style lang="stylus" scope src="@/assets/styles/common/Inserts.styl"></style>


